<template>
  <div class="hello">
    <div class="nowtime">当前时间：{{ formattedTime }}</div>
    <div style="height: 100px;"></div>
    <div style="display: flex; justify-content: center;">
      <div class="label">当前节次：</div><div class="nowjieci">{{ getJieCi }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CurrentDateTime',
  data() {
    return {
      timestamp: Date.now(), // 初始时间戳（毫秒）
      weekList: ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']  
    }
  },
  computed: {
    // 格式化为：2024年06月15日16时22分08秒
    formattedTime() {
      const date = new Date(this.timestamp)
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0') // 01~12
      const day = String(date.getDate()).padStart(2, '0')         // 01~31
      const hours = String(date.getHours()).padStart(2, '0')      // 00~23
      const minutes = String(date.getMinutes()).padStart(2, '0')  // 00~59
      const seconds = String(date.getSeconds()).padStart(2, '0')  // 00~59
      const weekday = this.weekList[date.getDay()]
      return `${year} 年 ${month} 月 ${day} 日 ${hours} 时 ${minutes} 分 ${seconds} 秒 ${weekday}`
    },
    // 转换为秒级时间戳
    timestampInSeconds() {
      return Math.floor(this.timestamp / 1000)
    },
    getJieCi() {
      const date = new Date(this.timestamp)
      // const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0') // 01~12
      // const day = String(date.getDate()).padStart(2, '0')         // 01~31
      const hours = String(date.getHours()).padStart(2, '0')      // 00~23
      const minutes = String(date.getMinutes()).padStart(2, '0')  // 00~59
      // const seconds = String(date.getSeconds()).padStart(2, '0')  // 00~59
      if (hours + minutes >= "0800" && hours + minutes <= "0840"){
        return "第一节"
      } else if(hours + minutes >= "0855" && hours + minutes <= "0935"){
        return "第二节"
      } else if(hours + minutes >= "0935" && hours + minutes <= "1000"){
        return "大课间"
      } else if(hours + minutes >= "1000" && hours + minutes <= "1040"){
        return "第三节"
      } else if(hours + minutes >= "1055" && hours + minutes <= "1135"){
        return "第四节"
      } 
      if (month == '05' || month == '06' || month == '07' || month == '08' || month == '09'){
        // 夏季时间
        if (hours + minutes >= "1430" && hours + minutes <= "1510"){
          return "第五节"
        } else if(hours + minutes >= "1525" && hours + minutes <= "1605"){
          return "第六节"
        } else if(hours + minutes >= "1620" && hours + minutes <= "1700"){
          return "第七节"
        } else if(hours + minutes >= "1700" && hours + minutes <= "1930"){
          return "晚饭"
        } else if(hours + minutes >= "1930" && hours + minutes <= "2010"){
          return "晚自习第一节"
        } else if(hours + minutes >= "2025" && hours + minutes <= "2105"){
          return "晚自习第二节"
        } 
      }else if(month == '10' || month == '11' || month == '12' || month == '01' || month == '02' || month == '03' || month == '04'){
        // 冬季时间
        if (hours + minutes >= "1400" && hours + minutes <= "1440"){
          return "第五节"
        } else if(hours + minutes >= "1455" && hours + minutes <= "1535"){
          return "第六节"
        } else if(hours + minutes >= "1550" && hours + minutes <= "1630"){
          return "第七节"
        } else if(hours + minutes >= "1630" && hours + minutes <= "1900"){
          return "晚饭"
        } else if(hours + minutes >= "1900" && hours + minutes <= "1940"){
          return "晚自习第一节"
        } else if(hours + minutes >= "1955" && hours + minutes <= "2035"){
          return "晚自习第二节"
        } 
      }
      return "课间"
    }
  },
  mounted() {
    // 每秒更新一次时间戳
    this.interval = setInterval(() => {
      this.timestamp = Date.now()
    }, 1000)
  },
  beforeUnmount() {
    // 组件销毁前清除定时器，避免内存泄漏
    clearInterval(this.interval)
  }
}
</script>

